import * as vscode from 'vscode';
import * as parser from '@babel/parser';
import traverse from '@babel/traverse';
import * as fs from 'fs';
import * as path from 'path';

async function parseJavaScriptFiles(dirPath: string): Promise<string[]> {
	vscode.window.showInformationMessage('Event 2222 ??????');
	const symbols: string[] = [];

	try {
		if (!fs.existsSync(dirPath)) {
			console.error(`Directory not found: ${dirPath}`);
			return symbols;
		}

		const files = await fs.promises.readdir(dirPath);

		for (const file of files) {
			if (file.endsWith('.js')) {
				const filePath = path.join(dirPath, file);
				const content = await fs.promises.readFile(filePath, 'utf8');
				const ast = parser.parse(content, {
					sourceType: 'module',
					plugins: ['jsx'],
				});

				traverse(ast, {
					VariableDeclarator(path) {
						if (path.node.id.type === 'Identifier') {
							symbols.push(path.node.id.name);
						}
					},
					FunctionDeclaration(path) {
						if (
							path.node.id &&
							path.node.id.type === 'Identifier'
						) {
							symbols.push(path.node.id.name);
						}
					},
				});
			}
		}

		return symbols;
	} catch (error) {
		console.error('Error parsing JavaScript files:', error);
		return symbols;
	}
}

export function activate(context: vscode.ExtensionContext) {
	const workspaceFolders = vscode.workspace.workspaceFolders;
	let dirPath = '';
	if (workspaceFolders) {
		dirPath = path.join(
			workspaceFolders[0].uri.fsPath,
			'force-app/main/default/lwc',
		);
	} else {
		vscode.window.showErrorMessage('No workspace folder is open');
		return;
	}

	parseJavaScriptFiles(dirPath).then((jsSymbols) => {
		const provider = vscode.languages.registerCompletionItemProvider(
			'html',
			{
				provideCompletionItems(
					document: vscode.TextDocument,
					position: vscode.Position,
				) {
					const lineText = document.lineAt(position).text;
					const cursorPosition = position.character;

					//  {}   
					const isWithinBraces =
						lineText.lastIndexOf('{', cursorPosition) >
						lineText.lastIndexOf('}', cursorPosition);

					if (!isWithinBraces) {
						return undefined;
					}

					const completionItems: vscode.CompletionItem[] = [];

					jsSymbols.forEach((symbol) => {
						const completionItem = new vscode.CompletionItem(
							symbol,
							vscode.CompletionItemKind.Function,
						);
						completionItem.detail = `JavaScript ${symbol}`;
						completionItem.insertText = symbol;
						completionItems.push(completionItem);
					});

					return completionItems;
				},
			},
			'{',
		);

		context.subscriptions.push(provider);
	});

	const watcher = vscode.workspace.createFileSystemWatcher(
		path.join(dirPath, '*.js'),
	);

	const disposable = vscode.workspace.onDidChangeTextDocument((event) => {
		const document = event.document;
		parseJavaScriptFiles(dirPath);
		vscode.window.showInformationMessage('Event 3333 ??????');
		if (document.languageId === 'javascript') {
			// : JavaScript  
			vscode.window.showInformationMessage(
				`File edited: ${document.fileName}`,
			);
			//      
			event.contentChanges.forEach((change) => {
				console.log(`Changed text: ${change.text}`);
			});
		}
	});

	vscode.window.showInformationMessage('Event 3333 ??????');
	watcher.onDidChange(() => {
		parseJavaScriptFiles(dirPath).then((updatedSymbols) => {});
	});
	context.subscriptions.push(watcher);
	context.subscriptions.push(disposable);
}

export function deactivate() {}
